.inputAuthContainer {
  position: relative;
  margin-top: 11px;
  padding: 15px 19px 15px 43px;
  border-radius: 14px;

  background-color: #F7F8F8;

  cursor: text;

  &:hover {
    background-color: #E7EEF9;
  }

  &.inputIsFocused {
    background-color: #E7EEF9;
  }
}

.inputAuth {
  width: 100%;

  background-color: transparent;

  font-size: 0.75rem;
  color: #5B5E65;

  &::placeholder {
    color: #89909B;
  }

  .darkTheme & {
    background-color: blue;
  }
}