@import 'src/index';

$regDecorativeLightImg: url('./images/light/register.svg');
$authDecorativeLightImg: url('./images/light/authorization.svg');
$regDecorativeDarkImg: url('./images/dark/register.svg');
$authDecorativeDarkImg: url('./images/dark/authorization.svg');

$regImgInitialWidth: 737px;
$regImgInitialHeight: 663px;
$authImgInitialWidth: 687px;
$authImgInitialHeight: 608px;

.regContainer {
  height: 100%;
  max-width: 1920px - (290*2);
  margin: 0 auto;

  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 14px;
}

.regBackground {
  height: 100vh;

  background-color: #F4F7FC;
}

.regFormWrapper {
  width: 414px;
}

.regDecorativeImg {
  background-image: $regDecorativeLightImg;
  background-repeat: no-repeat;
  width: $regImgInitialWidth;
  aspect-ratio: getAspectRatio($regImgInitialWidth, $regImgInitialHeight);

  body[id = 'dark'] & {
    background-image: $regDecorativeDarkImg;
  }
}

.authDecorativeImg {
  background-image: $authDecorativeLightImg;
  background-repeat: no-repeat;
  max-width: $authImgInitialWidth;
  aspect-ratio: getAspectRatio($authImgInitialWidth, $authImgInitialHeight);

  body[id = 'dark'] & {
    background-image: $authDecorativeDarkImg;
  }
}
